import React from "react"
import styled from "@emotion/styled"
import { css } from "@emotion/core"
import { Helmet } from "react-helmet"
import AdSense from "react-adsense"

import Nav from "./Nav"
import KatexStyled from "./KatexStyled"

const Content = styled.div`
  padding-top: 2rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  max-width: 740px;
  margin: 0 auto;

  p,
  ol,
  ul {
    line-height: 1.7;
  }

  a {
    color: var(--accent-color);
  }

  *:not(pre) > code {
    font-size: 0.875em;
    background-color: rgba(27, 31, 35, 0.05);
    padding: 0.25rem 0.5rem;
    margin: 0;
    border-radius: 3px;
  }

  h1 {
    font-size: 2.2rem;
  }

  h3 {
    font-size: 1.35rem;
  }

  h2 {
    font-size: 1.65rem;
    padding-bottom: 0.3rem;
    border-bottom: 1px solid #eaecef;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    padding-top: 3rem;
    margin-top: -2rem;
  }

  img {
    max-width: 100%;
  }

  pre {
    line-height: 1.4;
    padding: 1.25rem 1.5rem;
    margin: 0.85rem 0;
    border-radius: 6px;
    overflow: auto;
    font-size: 0.875rem;
    @media (max-width: 419px) {
      border-radius: 0px;
      margin: 0.85rem -1.5rem;
    }
  }

  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New,
      monospace;
  }

  /* tableOfContents injects p under li */
  li > p {
    margin: 0;
  }

  blockquote {
    font-size: 0.9rem;
    color: #888;
    border-left: 0.5rem solid #dfe2e5;
    margin: 0.5rem 0;
    padding: 0.25rem 0 0.25rem 1rem;
    > p {
      margin: 0;
    }
  }

  (max-width: 419px) {
    pre {
      margin: 0.85rem -1.5rem;
      border-radius: 0;
    }
  }
`

const ContentFooter = ({ post }) => {
  const { lastmod } = post.frontmatter
  if (!lastmod) {
    return null
  }
  return (
    <div
      css={css`
        margin-top: 1rem;
        margin-bottom: 1rem;
        display: flex;
        justify-content: flex-end;
        font-size: 0.875rem;
        color: #888;
      `}
    >
      <div>
        <b>最終更新:</b> {new Date(lastmod).toLocaleDateString("ja")}
      </div>
    </div>
  )
}

const ContentBody = ({ post }) => {
  if (!post.frontmatter.enable_toc) {
    return <div dangerouslySetInnerHTML={{ __html: post.html }} />
  }
  const match = /<h2[>\s]/.exec(post.html)
  if (!match) {
    return <div dangerouslySetInnerHTML={{ __html: post.html }} />
  }
  // TODO(yunabe): File a bug about '.html/#' bug.
  return (
    <div>
      <div
        dangerouslySetInnerHTML={{ __html: post.html.substr(0, match.index) }}
      />
      <p>
        <b>目次</b>
      </p>
      <div
        dangerouslySetInnerHTML={{
          __html: post.tableOfContents.replace(/\.html\/#/g, ".html#"),
        }}
      />
      <div
        dangerouslySetInnerHTML={{ __html: post.html.substr(match.index) }}
      />
    </div>
  )
}

const HelmetHeader = ({ post, site }) => {
  return (
    <Helmet>
      <title>{post.frontmatter.title + " | " + site.siteMetadata.title}</title>
    </Helmet>
  )
}

export default ({ data }) => {
  const post = data.markdownRemark
  const site = data.site
  if (/class=["']?katex/.test(post.html)) {
    return (
      <KatexStyled>
        <HelmetHeader post={post} site={site} />
        <Nav slug={post.fields.slug} />
        <main>
          <Content>
            <h1>{post.frontmatter.title}</h1>
            <ContentBody post={post} />
            <ContentFooter post={post} />
          </Content>
        </main>
      </KatexStyled>
    )
  }
  return (
    <div>
      <HelmetHeader post={post} site={site} />
      <Nav slug={post.fields.slug} />
      <main>
        <Content>
          <h1>{post.frontmatter.title}</h1>
          <ContentBody post={post} />
          <ContentFooter post={post} />

        </Content>
      </main>
    </div>
  )
}
